.card {
  width: 100%;
  height: 440px;
  margin-bottom: 20px;

  @include adaptive-size(tablet) {
    width: 294px;
    margin: 0 30px 30px 0;
  }
  @include adaptive-size(desktop) {
    width: 274px;
  }

  &:hover,
  &:focus {
    transform: scale(1.05);
    transition: transform 0.3s ease-in-out 0s;

    .card__title {
      color: $accent-lighter-color;
    }
  }
}

.card__tumb {
  height: 398px;
  overflow: hidden;
}

.card__image {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: $radius;
}

.card__title {
  text-transform: uppercase;
  font-size: 12px;
  line-height: 1.17;
  padding-top: 10px;
  transition: color 0.3s ease-in-out 0s;

  @include adaptive-size(tablet) {
    line-height: 1.33;
  }
}

.card__caption {
  @extend %flex-centered;
  justify-content: start;

  font-size: 12px;
  line-height: 1.33;
  color: $accent-darker-color;
  padding: 1px 0;
}
.card__items {
  display: flex;
  justify-content: start;
  margin: right 3px;
}

.card__item {
  margin-right: 5px;
  &:not(:last-child) {
    margin-right: 3px;
    &::after {
      content: ',';
      display: inline-block;
    }
  }
}

.card__year {
  width: 33px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: unset;

  &::before {
    content: '';
    display: inline-block;
    width: 1px;
    height: 11px;
    margin-right: 5px;
    background-color: $accent-darker-color;
  }
}

.card__rating {
  margin-left: 10px;
  color: $text-white-color;
  line-height: 14px;
  height: 16px;
  width: 36px;
  padding-top: 1px;
  text-align: center;
  background-color: $accent-lighter-color;
  border-radius: $radius;
}
